import React from 'react';
import './App.css';

class Assaults extends React.Component {
    constructor(props) {
        super(props);
        this.state = {now: new Date()};
    }
    componentDidMount() {
        this.timerID = setInterval(
          () => this.tick(),
          1000
        );
    }
    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({
            now : new Date()
        });
    }
    
    render() {
        startTime()
        return (            
            <div className="assaultspage" style={{whiteSpace: 'pre-wrap'}}>
                <h1>Assaults</h1>
                <h2>決戰艾澤拉斯-衝突戰時刻表</h2>
                <p id="ass_l">{data.p1}</p>               
                <p id="ass_l">{data.p2}</p>
                <p id="ass_s">{data.p3}</p>                
                <p id="ass_s">{data.p4}</p>
                
            </div>
        );
    }
    
}
var data={
    p1:"準備/開戰",
    p2:"時間",
    p3:"next",
    p4:"more",
}
export default Assaults;
function startTime(){
    var oriTime=new Date(2020,1,25,7);//原為GTM+8 7點
    //68400000為19小時  19小時1週期(7h開戰12h等待)
    var now = new Date(Date.now()-oriTime.getTime());
    var round=parseInt(now.getTime()/68400000);
    var rem=now.getTime()%68400000;
    
    var ass_timer=new Date();
    if(rem<25200000){//25200000為7h 週期內的終戰分界
        ass_timer=new Date(-3600000-rem);//GTM+8 故19-8 改為11小(39600000)        
        data.p1 = "開戰中\n"+ass_area(round);
    }else{     
        ass_timer=new Date(39600000-rem);//GTM+8 故7-8 改為-1小(-3600000)
        data.p1 = "備戰中\n";
    }
    


    var hh = ass_timer.getHours();
    var mm = ass_timer.getMinutes();
    var ss = ass_timer.getSeconds();
    mm = checkTime(mm);
    ss = checkTime(ss);

    
    
    data.p2 = hh + ":" + mm + ":" + ss+"\n\n";
    data.p3 = "下次地點:"+ass_area(round+1)+"\n"+new Date((round+1)*68400000+1582585200000);//1582585200000起點時間
    data.p4 = "\n"+ass_time(new Date((round+2)*68400000+1582585200000))+"  "+ass_area(round+2)
                                                +"\n"+ass_time(new Date((round+3)*68400000+1582585200000))+"  "+ass_area(round+3);
    setTimeout(startTime, 500);
    //timeout->重新抓時間
}

function checkTime(i){
    if(i < 10) {
      i = "0" + i;
    }
    return i;
}

function ass_area(i){
    var aa="";
    switch(i%6){
        case 0:
            aa="提拉加德海灣";
            break;
        case 1:
            aa="納茲米爾";
            break;
        case 2:
            aa="斯陀頌恩谷地";
            break;
        case 3:
            aa="沃魯敦";
            break;
        case 4:
            aa="佐斯瓦";
            break;
        case 5:
            aa="祖達薩";
            break;
        default:
            aa="error"
    }
    return aa;
}
function ass_time(date){
    var y=date.getFullYear();
    var mo=date.getMonth()+1;
    var d=date.getDate();
    var h = date.getHours();
    var m = date.getMinutes();
    var s = date.getSeconds();
    m = checkTime(m);
    s = checkTime(s);
    return y+"."+mo+"."+d+" "+h+":"+m+":"+s;
}