import React from 'react';
import './App.css';
import Assaults from './Assaults';
import AIR from './Air';


class Body extends React.Component {
    
    render() {
        let {index}=this.props;
        return (
            
            <div className="Body1">
                <header className="Body1-main">
                    {pagelist(index)}
                    
                    
                </header>
            </div>
        );
    }
}
export default Body;
function pagelist(index="Home"){
    switch(index){
        case 'Home':
            return (<Home  />);
        case 'Source':
            return (<Source  />);
        case 'Assaults':
            return (<Assaults  />);
        case 'AIR(AQI)':
            return (<AIR  />);
        case 'Coming..':
            return (<Home  />);
        default:
            return (<Home  />);
    }
}


class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            text:""
        };
    }
    handler(e){
        var value=e.target.value
        this.setState({text:value})
    }
    render() {
        return (            
            <div className="homepage" >
                
                <h1>Home</h1>
                <p>React練習<br></br>寫程式好難</p>
                <img src=".\image\WDWrBTO.jpg" className="Page-logo" alt="gitlogo" style={{height: "50vh"}}/>
                <br></br>
                <input
                    text="text"
                    value={this.state.text}
                    onChange={this.handler.bind(this)}
                />
                <p>{this.state.text}</p>
            </div>
        );
    }
}
class Source extends React.Component {
    render() {
        return (            
            <div className="sourcepage">
                <h1>Source</h1>
                <p>我懶得<br></br>放東西了</p>
            </div>
        );
    }
}

