import React from 'react';
import './App.css';
import Body1 from './Body1';
var pagedex=[
  "Home",
  "Source",
  "Assaults",
  "AIR(AQI)"
]

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        index : pagedex[0]
    };
  }
  pagefontWeight(pagename){
    if (pagename===this.state.index) {
      return "bolder"
    }
    return "lighter"
  }
  
  render() {    
    return (
      <div className="Page">
        <header className="Page-header">
          <nav className="Page-nav">
            <img src="GitHub-Mark-Light-120px-plus.png" className="Page-logo" alt="gitlogo" />
            <ul className="Page-ul">
              <li className="Page-li_1" onClick={ () => this.setState({index : pagedex[0]})}style={{fontWeight:this.pagefontWeight(pagedex[0])}}>cucome1314#</li>
              <li className="Page-empty"></li>
              <li className="Page-empty"></li>
              <li className="Page-li_2" onClick={()=> this.setState({index : pagedex[1]}) } style={{fontWeight:this.pagefontWeight(pagedex[1])}}>{pagedex[1]}</li>
              <li className="Page-li_3" onClick={()=> this.setState({index : pagedex[2]})}style={{fontWeight:this.pagefontWeight(pagedex[2])}}>{pagedex[2]}</li>
              <li className="Page-li_4" onClick={()=> this.setState({index : pagedex[3]})}style={{fontWeight:this.pagefontWeight(pagedex[3])}}>{pagedex[3]}</li>
              <li className="Page-li_5" >Coming..</li>
            </ul>
          </nav>
          
        </header>
        <Body1 index={this.state.index}/>
        <div className="Page_bottom" style={{background: "#282c34", height: "10vh", "fontSize": "10px",  "textAlign": "center"}}>
            I don't own any copyright
            <br></br>cucome1314# I suck at programming 
        </div>
      </div>
    );
  }
}
export default Page;