import React from 'react';
import './App.css';
import exampledata from './tycg_air_new.json';
import fetchJsonp from 'fetch-jsonp';

var data = {
    p1: "空氣品質小時值_桃園市_龍潭站",
    arrLists: ["connecting"]
}
class AIR extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoaded: false,
            apidata: []
        };
        this._isMounted = false;
    }
    componentDidMount() {
        this._isMounted = true;
        fetchJsonp("https://opendata.epa.gov.tw/webapi/api/rest/datastore/355000000I-000259?filters=SiteId eq '21'&sort=SiteName&offset=0&limit=1000&format=json&token=PfRByb0uMUi8Jiuu9DwDTA")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        apidata: result.result.records
                    });
                    
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }
    componentWillUnmount() {
        this._isMounted = false;
    }
    render() {

        var tar_api = ['PM2.5', 'NO2', 'PM10', 'O3', 'CO', 'SO2'];
        if(this.state.isLoaded){
            data.p1 = '空氣品質小時值_桃園市_龍潭站\n最後更新時間:' + this.state.apidata[0]["PublishTime"] + "\n空氣品質:" + this.state.apidata[0]["Status"] + "\nAQI:" + this.state.apidata[0]["AQI"];
            for (let i = 0; i < 6; i++) {
                data.arrLists[i] = (<li>{exampledata[i].ItemName + '(' + exampledata[i].ItemEngName + '):' + this.state.apidata[0][tar_api[i]] + exampledata[i].ItemUnit+"\n"}</li>)
            }
        }
        

        return (
            <div className="airpage" style={{ whiteSpace: 'pre-wrap' }}>
                <h1>AIR(AQI)</h1>
                <p>{data.p1}</p>
                <ul>
                    {data.arrLists[0]}
                    {data.arrLists[1]}
                    {data.arrLists[2]}
                    {data.arrLists[3]}
                    {data.arrLists[4]}
                    {data.arrLists[5]}
                </ul>
            </div>
        );
    }
}
export default AIR;